import { listOfAddedMovies, displayStartPage } from '../composables/mainCards';
import { projectAuth } from '../../firebase/config';
import { projectFirestore } from '../../firebase/config';

// const { user } = getUser();

const refs = {
  homeBtn: document.querySelector('.link-home'),
  myLibraryBtn: document.querySelector('.link-library'),
  mainSection: document.querySelector('.js-ul-film'),
  libraryUl: document.querySelector('.my-library__list'),
  librarySection: document.querySelector('.my-library'),
  queueLibBtn: document.querySelector('.queue-btn'),
  watchedLibBtn: document.querySelector('.watched-btn'),
};

let libArray = [];

const getLibrary = data => {
  if (data.length) {
    libArray = data.map(doc => doc.data());
  }

  // console.log('lib: ', libArray);
};

export default getLibrary;

// refs.myLibraryBtn.addEventListener('click', event => {
//   listOfAddedMovies(libArray);
// });

refs.homeBtn.addEventListener('click', event => {
  // refs.librarySection.classList.add('hidden');
  displayStartPage();
});

let libWatched = [];
let libQueue = [];

refs.watchedLibBtn.addEventListener('click', event => {});

projectAuth.onAuthStateChanged(_user => {
  if (_user) {
    projectFirestore
      .collection('queue')
      .orderBy('createdAt')
      .onSnapshot(snapshot => {
        libQueue = snapshot.docs.map(doc => doc.data());
      });

    projectFirestore
      .collection('watched')
      .orderBy('createdAt')
      .onSnapshot(snapshot => {
        libWatched = snapshot.docs.map(doc => doc.data());
      });

    refs.myLibraryBtn.addEventListener('click', event => {
      refs.queueLibBtn.classList.add('active-lib-btn');
      refs.watchedLibBtn.classList.remove('active-lib-btn');
      listOfAddedMovies(libQueue);
    });

    refs.watchedLibBtn.addEventListener('click', event => {
      event.target.classList.add('active-lib-btn');
      refs.queueLibBtn.classList.remove('active-lib-btn');
      listOfAddedMovies(libWatched);
    });
    refs.queueLibBtn.addEventListener('click', event => {
      event.target.classList.add('active-lib-btn');
      refs.watchedLibBtn.classList.remove('active-lib-btn');
      listOfAddedMovies(libQueue);
    });
  } else {
    console.log('user logged out');
    libQueue = [];
    libWatched = [];
    // setupUI();

    refs.myLibraryBtn.addEventListener('click', event => {
      listOfAddedMovies(libQueue);
      refs.mainSection.innerHTML =
        '<h3 style="margin:0 auto;">Please Login to See/Create Library☺</h3>';
    });

    refs.watchedLibBtn.addEventListener('click', event => {
      event.target.classList.add('active-lib-btn');
      refs.queueLibBtn.classList.remove('active-lib-btn');
    });

    refs.queueLibBtn.addEventListener('click', event => {
      event.target.classList.add('active-lib-btn');
      refs.watchedLibBtn.classList.remove('active-lib-btn');
    });
  }
});
